<template>
	<div>
		<div class="container">
			<div class="header">
				<span @click="back">取消</span>
				<span>个性签名</span>
				<span @click="sure">确定</span>
			</div>
			<div class="area">
				<div>
					<textarea name="familyAddr" @input="input_content" v-model="params.intro" id="familyAddr" rows="4" placeholder="5-60个字符,且不可以全部是数字"></textarea>
				</div>
				<div class="enter_letter">你还可以输入{{comFontNum > 0 ? comFontNum : 0}}个字</div>
			</div>
		</div>
		<success v-if="success" :title="title"></success>
	</div>
</template>
<script>
	import success from '@/components/success'
	export default {
		components: {
			success
		},
		data() {
			return {
				show: false,
				params: {
					mobile: null,
					userId: '',
					intro: ''
				},
				allowSend: true,
				font_num: 60,
				title: '保存中',
				faste_title: 0,
				success: 0
			}
		},
		computed: {
			comFontNum() {
				if(!this.params.intro){
					return this.font_num;
				}
				return this.font_num - this.params.intro.length;
			}
		},
		methods: {
			input_content() {
				if (this.comFontNum <= 0) {
					this.params.intro = this.params.intro.substring(0, 60)
				}
			},
			back() {
				this.$router.go(-1);
			},
			sure() {
				this.$Indicator.open({
					text: '修改中...',
					spinnerType: 'fading-circle'
				})
				this.faste_title = 1;
				this.params.mobile = this.$route.params.phone;
				this.params.userId = this.$route.params.id;
				this.axios.post('/api/changeIntro', this.params).then((data) => {
					if (data.data.status == 'success') {
						this.$Indicator.close()
						this.$Toast({
							message: '修改成功',
							position: 'middle',
							duration: 1000
						})
						this.$store.state.userModule.LOGIN_USER_OBJECT.user.introduction =  this.params.intro
						this.$store.state.userModule.USER_INFO_OBJECT.introduction =  this.params.intro
						this.faste_title = 0
						setTimeout(() => {
							this.$router.push({
								name: 'baseInfo',
								params: {
									id: this.params.userId
								}
							});
						}, 1500)
					}
				}).catch((err) => {
					console.log(err)
				});
			}
		},
		mounted() {
			this.params.intro = this.$route.params.intro
		}
	}
</script>
<style lang="less" scoped>
	@signatureHeight: 100px;
	.container {
		background-color: #eee;
	} // 头部
	.header {
		display: flex;
		height: @signatureHeight;
		background-color: #fff;
		div {
			flex: 2;
			padding-left: 20px;
			height: @signatureHeight;
		}
		span {
			flex: 3.333;
			display: inline-block;
			text-align: center;
			height: @signatureHeight;
			line-height: @signatureHeight;
			&:nth-child(1) {
				color: #999;
				text-align: left;
				text-indent: 1em;
				font-size: 25px;
			}
			&:nth-child(2) {
				font-size: 40px;
			}
			&:nth-child(3) {
				text-align: right;
				padding-right: 20px;
				color: #44b336;
				box-sizing: border-box;
				font-size: 25px;
			}
		}
	} // 头部
	.area {
		padding: 50px 30px;
		&>div {
			background-color: #fff;
			padding: 5px;
			span {
				width: 10%;
				display: inline-block;
				text-align: center;
				height: 300px;
			}
			textarea {
				width: 100%;
				border: none;
				outline: none;
				resize: none;
				height: 300px;
				font-size: 30px;
				padding: 0;
				&::-webkit-input-placeholder {
					color: #bbb;
					font-size: 30px;
				}
			}
		}
		&>.enter_letter {
			width: 100%;
			height: 100px;
			line-height: 100px;
			background-color: #fff;
			color: #aaa;
			text-align: right;
			box-sizing: border-box;
			font-size: 25px;
		}
	}
</style>





